.project-detail {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin: 1rem;
  background-color: var(--color-background);
  padding: 1rem;
  border-radius: 8px;
}

.project-detail-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--color-secondary);
  color: var(--color-text-light);
  padding: 0.5rem 1rem;
  border-radius: 4px;
  min-height: 4rem;
}

.header-top {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.back-button {
  border: 1px solid var(--color-text-light);
  background: var(--color-primary);
  color: var(--color-text-light);
  padding: 0.5rem 1rem;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s ease;
}

.back-button:hover {
  background: var(--color-background);
  color: var(--color-text-dark);
  border: 1px solid var(--color-primary);
}

.desktop-only {
  display: block;
}

.mobile-only {
  display: none;
}

.header-controls {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.header-controls button {
  padding: 0.5rem 1rem;
  cursor: pointer;
  border-radius: 4px;
  border: 1px solid var(--color-text-light);
  background: var(--color-primary);
  color: var(--color-text-light);
  margin-right: 0.5rem;
}

.header-controls button:hover {
  background: var(--color-background);
  color: var(--color-text-dark);
}

.scatterplot-area {
  min-height: 300px;
  border: 1px solid var(--color-secondary);
  background: var(--color-text-light);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: var(--color-text-dark);
}

button {
  border: 1px solid var(--color-text-light);
  background: var(--color-primary);
  color: var(--color-text-light);
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background: var(--color-background);
  color: var(--color-text-dark);
  border: 1px solid var(--color-primary);
}

@media (max-width: 600px) {
  .project-detail-header {
    flex-direction: column;
    align-items: center;
    gap: 1rem;
    /* margin: 0.5rem; */
    padding: 1rem;
    border-radius: 8px;
  }
  
  .header-controls {
    margin: 0.5rem 0 0.5rem 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  
  .desktop-only {
    display: none;
  }
  
  .mobile-only {
    display: block;
    width: 100%;
    text-align: left;
    /* margin-top: 1.5rem; */
  }
}